@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:200,300,400,500,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Montserrat';
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

/* .condiv class is for the right side content container */

.condiv {
  padding: 100px;
  background-color: #f4f5f9;
  flex: 6 1;
  flex: var(--condiv-flex) 1;
}

.condiv a {
  color: black;
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.profilepic {
  border-radius: 50%;
  width: 300px;
  margin: 30px;
}

.typingeffect {
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

nav {
  padding: 60px, 30px, 0px, 30px;
  flex: 1.5 1;
  flex: var(--sidebar-flex) 1;
  background: rgb(68,70,73);
  background: linear-gradient(180deg, rgb(50, 52, 54) 50%, rgb(25, 26, 27) 80%);
  text-align: center;
}

nav ul {
  font-size: 1.6rem;
  font-size: var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}

nav ul li {
  margin: 40px;
}

nav a:hover {
  color: #ffc403;
}

a {
  text-decoration: none;
  color: white;
}

/* css for active class */
.active {
  font-weight: 600;
}

.social {
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}

.social i {
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}

.social i:hover{
  color: #ffffff;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
}

h1, h2, h3, h4, p {
  margin: 10px;
}

h1, h2, h3, h4 {
  font-weight: 500;
}

p {
  font-weight: 400;
  line-height: 1.8rem;;
}

h1 {
  border-bottom: solid 4px #ffc403;
  line-height: 3.5rem;
}

h2 {
  font-size: 2rem;
}

.widecard {
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
}

.widecard:hover {
  color: #1b1d20;
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.skills ul li {
  list-style-type: none;
  margin: 30px 30px 30px 10px;
}

.columns {
  display: inline;
}

.left {
  float: left;
  margin-right: 50px;
}

.right {
  float: left;
}

.bullet {
  display: inline;
}

.icon {
  height: 1.6rem;
  width: 1.6rem;
  vertical-align: middle;
  margin-right: .5rem;
}

.condivp {
  padding: 100px 0 0 0;
  margin: 0;
  background: #f4f5f9;
  display: inline-block;
  width: 100vw;
  overflow-x: hidden;
}

.row {
  padding: 0;
  margin: 0;
  width: 100vw;
}

.column {
  padding: 0;
  margin: 0;
  float: left;
  max-width: 100%;
}

.portfolio {
  width: 100%;
  display: block;
}

.logo {
  padding-top: 60px;
  height: 150px;
  width: auto;
}

@media all and (max-width: 750px) {
  .columns {
    display: block;
  }

  .icon {
    display: none;
  }

  .left {
    float: none;
  }

  .right {
    float: none;
  }

  nav {
    padding: 60px, 10px, 0px, 10px;
    flex: 1em 1;
    background: rgb(68,70,73);
    background: linear-gradient(180deg, rgb(50, 52, 54) 50%, rgb(25, 26, 27) 80%);
  }
  
  nav ul {
    font-size: 1em;
    text-align: right;
    list-style-type: none;
    text-decoration: none;
    color: white !important;
  }
  
  nav ul li {
    margin: 40px 20px 40px 20px;
  }

  .column {
    padding: 0;
    margin: 0;
    float: left;
    min-width: 100%;
  }

  .logo {
    padding-top: 60px;
    max-width: 90%;
    height: auto;
  }

}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }

  .App {
    flex-direction: column;
  }

  .sidebar {
    padding: 0;
    background: rgb(68,70,73);
    background: linear-gradient(180deg, rgb(50, 52, 54) 50%, rgb(35, 36, 37) 80%);
    margin: 0;
  }

  .condiv {
    padding: 50px ;
  }

  nav ul {
    display: inline;
    text-align: center;
  }

  nav ul li {
    margin: 0px 0 30px 0;
  }

  .social {
    left:0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
    position: relative;
  }

  .social i {
    padding-right: 0px;
  }

  .logo {
    margin-top: 0px;
    margin-bottom: 40px;
  }

  .typingeffect {
    font-size: 1.2rem;
    margin: 0 0 50px 0;
    padding: 0;
    text-align: center;
  }

  .profilepic {
    border-radius: 50%;
    width: 250px;
    height: 250px;
    margin: 30px;
  }

}


